.functions-content {
    padding-bottom: 30px;
    padding-left: 14px;
}

.img-content {
    width: 58.33333333%;
    float: left;
}

.card-space {
    width: 41%;
}

@media only screen and (max-width:768px) {
    .functions-content {
        margin: 0;
        width: 100%;
        display: block;
        position: relative;
        height: 400px;
    }
    .img-content {
        float: none;
        width: 100%;
        margin-bottom: 20%;
    }
    .img-content figure {
        margin: 0;
    }
    .card-space {
        width: 100%;
    }
}