.container {
  margin: auto 5%;
  background-image: url("./assets/background.png");
}

.row {
  width: 98%;
  margin: 10px;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}

.section-header {
  text-align: center;
}

figure {
  text-align: center;
  vertical-align: middle;
}

img {
  width: auto;
  max-width: 100%;
  height: auto;
}

.block {
  display: inline-block;
  padding-left: 0px !important;
}

.float-right {
  float: right !important;
}

.float-left {
  float: left !important;
}

.left {
  left: 18% !important;
  margin-top: auto;
}

@media only screen and (max-width:768px) {
  .container {
    margin: 0;
  }
  .row {
    display: block;
  }
}