.images {
    float: left;
    border-style: solid;
    border-width: 1px;
    padding: 20px;
    margin: 5px;
    height: 250px;
    width: 243px;
    overflow: hidden;
    text-align: center;
}

.clearfix {
    clear: both;
}

.gallery-content {
    height: 475px;
    display: block;
    width: 100%;
    overflow: auto;
    border: 2px solid grey;
}

@media only screen and (max-width:768px) {
    .images {
        width: auto;
    }
}