.vertical-divider {
    left: 50%;
    background: #9E0B1D;
    position: absolute;
    height: 80%;
    width: 1px;
    display: block;
    text-align: center;
}

.vertical-divider::before {
    content: "";
    background: url(../../assets/dot.png) no-repeat !important;
    width: 36px;
    position: absolute;
    right: -16px;
    text-align: center;
    height: 36px;
}

.vertical-divider::after {
    content: "";
    background: url(../../assets/dot.png) no-repeat !important;
    width: 36px;
    position: absolute;
    right: -16px;
    text-align: center;
    height: 36px;
    bottom: 0
}

.left-section {
    padding-right: 20px;
    width: 47%;
    float: left;
    left: 0;
}

.met-section {
    border: 1px solid #f3d5af;
    padding: 40px 32px 20px 33px;
    text-align: right;
    margin-bottom: 99px;
    position: relative;
    background: #ffffff;
}

.met-section {
    border-color: #9E0B1D;
    text-transform: none !important;
}

.met-section p {
    padding-bottom: 0px;
    line-height: 1.5;
    font-family: roboto;
    font-weight: 300;
}

.met-section p {
    text-align: left !important;
}

.met-section::after {
    content: "";
    background: url(../../assets/dot.png) no-repeat !important;
    width: 36px;
    position: absolute;
    right: -55px;
    text-align: center;
    height: 36px;
    top: 160px;
}

.arrow-cls {
    background: #9E0B1D;
    padding: 8px 15px;
    display: inline-block;
    color: #555555;
    position: relative;
    margin-right: 16px;
    margin-bottom: 27px;
    border-radius: 6px;
}

.arrow-cls::before {
    content: "";
    width: 400px;
    height: 35px;
    position: absolute;
    right: -77px;
    top: 5px;
    display: inline-block;
}

.shape {
    position: relative;
    font-family: roboto;
    text-transform: uppercase;
    color: #ffffff;
    padding: 2px;
    font-weight: 800;
}

.shape::before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-top: 17px solid transparent;
    border-right: 15px solid #9E0B1D;
    border-bottom: 17px solid transparent;
    left: -29px;
    top: -7px;
}

.shape::after {
    width: 0;
    content: "";
    position: absolute;
    height: 0;
    border-top: 17px solid transparent;
    border-left: 15px solid #9E0B1D;
    border-bottom: 17px solid transparent;
    right: -29px;
    top: -7px;
}

.right-section {
    padding-top: 144px;
    width: 47%;
    padding-left: 20px;
    position: absolute;
    right: 0;
}

.story-content::after {
    clear: both;
    content: ' ';
    display: block;
    font-size: 0;
    line-height: 0;
    visibility: hidden;
    width: 0;
    height: 0;
}

.met-section-right {
    text-align: left;
}

.met-section-right::after {
    left: -55px;
    top: 210px;
}

.right-arrow-cls {
    margin-left: 16px;
    margin-bottom: 37px;
}

.right-arrow-cls::before {
    top: -10px;
    left: -77px;
    top: -9px;
    -ms-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}

.story-content {
    width: 100%;
    position: relative;
    display: block;
    height: 650px;
    margin-top: 25px;
}

@media only screen and (max-width:768px) {
    .left-section {
        width: 44%;
    }
    .met-section {
        padding: 10%;
        margin-bottom: 0;
    }
    .met-section figure {
        margin: 0;
    }
    .met-section::after {
        right: -41px;
        top: 143px;
    }
    .right-section {
        width: 44%;
    }
    .met-section-right::after {
        left: -41px;
        top: 177px;
    }
}