.header {
    height: 110px;
    background-color: #9E0B1D;
    width: 100%;
}

.icon {
    width: 10%;
    display: inline-block;
    height: 100%;
}

.header img {
    display: block;
    width: 100%;
}

.nav {
    float: right;
    height: 100%;
}

.nav ul {
    list-style: none;
    margin-top: 5%;
}

.nav ul li {
    display: inline-block;
    margin: 10px;
}

.nav ul li a {
    color: #e8cc53;
    text-decoration: none;
    font-family: cursive;
    font-weight: bolder;
    font-size: 22px;
}

@media only screen and (max-width:768px) {
    .icon {
        width: 30%;
    }
    .nav {
        width: 70%;
        overflow: auto;
    }
}