.guest-form {
    margin-top: 15px;
    background-color: #E8CC53 !important;
    padding: 42px 100px 35px;
    background-image: url(../../assets/icon-left-top.png), url(../../assets/icon-right-top.png), url(../../assets/icon-left-bottom.png), url(../../assets/icon-right-bottom.png);
    background-position: 17px 17px, 98% 17px, 14px 97%, 98% 97%;
    background-repeat: no-repeat;
}

.guest-form input, textarea {
    border-radius: 0;
    width: 46%;
    padding: 0px 12px 0px;
    border: 2px solid #ece5ce;
    float: left;
    line-height: 53px;
    height: 52px;
    color: #fff;
    font-size: 16px;
    font-weight: 300;
    background: transparent;
    font-family: cursive;
}

.guest-form input[type="email"] {
    margin-left: 1%;
    width: 46.5%;
}

.guest-form input[type="radio"] {
    margin-left: 5%;
    width: 4%;
    height: 30px;
}

.guest-form textarea {
    width: 96%;
    height: 150px;
}

.options label {
    margin-top: 4px;
    font-size: 20px;
    font-family: cursive;
    font-weight: 600;
    color: rgb(232, 235, 237);
}

.options {
    height: 60px;
}

.ques {
    width: 30%;
    font-size: 20px;
    font-weight: 600;
    font-family: cursive;
}

.add-to-guest {
    font-size: 16px;
    font-weight: 600;
    padding: 13px 44px;
    border-color: #fff;
    background: transparent;
    color: white;
}

.msg {
    width: 40% !important;
    cursor: pointer;
}

.hide {
    display: none;
}

@media only screen and (max-width:768px) {
    .guest-form {
        padding: 10%;
    }
    .guest-form input, textarea {
        width: 90%;
        margin-bottom: 5px;
        display: block;
    }
    .guest-form input[type="email"] {
        width: 90%;
        margin-left: 0;
    }
    .guest-form textarea {
        width: 90%;
    }
    .options {
        display: flex;
    }
    .options label {
        margin-top: 0;
        font-size: 13px;
    }
}