.card {
    width: 408px;
    background-image: url(../../assets/icon-left-top.png), url(../../assets/icon-right-top.png), url(../../assets/icon-left-bottom.png), url(../../assets/icon-right-bottom.png);
    background-position: 5px 4px, 99% 4px, 5px 99%, 99% 99%;
    background-repeat: no-repeat;
    width: 500px;
    border: 2px solid #c89f30;
    background-color: #fff;
    position: relative;
    margin-bottom: 71px;
    right: 20%;
    margin-top: 13%;
}

.day, .time, .address, .venue {
    position: relative;
    font-size: 19px;
    text-transform: uppercase;
    display: block;
    line-height: 1.5;
    font-family: roboto;
    color: darkolivegreen;
    letter-spacing: 1px;
}

.event-description day::before {
    content: "ÔÅ≥";
    font-family: FontAwesome;
    font-size: 18px;
    padding-left: 1px;
    position: absolute;
    right: 102%;
    text-align: center;
}

.time {
    text-transform: none;
}

.address {
    padding-top: 10px;
    text-transform: capitalize;
}

.event-description p {
    font-size: 16px;
    font-weight: 300;
    font-family: roboto;
    line-height: 1.5;
    padding-bottom: 30px;
    padding-top: 15px;
    color: #000000 !important;
}

.event-description h3 {
    color: #c89f30;
    font-size: 30px;
    font-family: cursive;
}

@media only screen and (max-width:768px) {
    .card {
        position: absolute;
        top: 33%;
        right: 15%;
        width: 70%;
        height: 220px;
        margin: 0;
    }
    .event-description h3 {
        margin-bottom: 14px;
        margin-top: 16px;
    }
    .day{
        font-size: 14px;
        font-weight: bold;
    }
    .time{
        font-size: 12px;
    }
    .address,.venue{
        font-size: 13px;
    }
}