.travels-content {
    text-align: left;
}

.travels-content h3 {
    margin-top: 20px;
    margin-bottom: 10px;
    color: #c89f30;
    font-family: cursive;
    letter-spacing: 2px;
    font-size: 25px;
}

.travels-content p {
    color: #757575;
    line-height: 1.5;
    font-size: 19px;
    font-weight: 400;
    font-family: cursive;
}

@media only screen and (max-width:768px) {
    .travels-content p {
        padding: 0 10px;
    }
}